import React from 'react'
import { connect } from "react-redux";
import { follow, setCurrentPage, unfollow, toggleFollowingProgress, requestUsers} from '../../redux/users-reducer';
import Users from './Users';
import Preloader from '../common/Preloader/Preloader';
import { compose } from 'redux';
import { getPagesSize, getUsers, getTotalUsersCount, getCurrentPage, getIsFetching, getFollowingInProgress, getUsersSuper } from '../../redux/users-selectors';

class UsersContainer extends React.Component{

    componentDidMount(){
        let {currentPage, pagesSize} = this.props
        this.props.requestUsers(currentPage, pagesSize);
    }

    onPageChanged = (pageNumber) =>{
    let {pagesSize} = this.props
        this.props.requestUsers(pageNumber, pagesSize)
    }


    render(){
        return<>
        {this.props.isFetching ? <Preloader /> : null}
        <Users 
        totalUsersCount={this.props.totalUsersCount}
        pagesSize={this.props.pagesSize}
        currentPage={this.props.currentPage}
        onPageChanged={this.onPageChanged}
        users={this.props.users}
        follow={this.props.follow}
        unfollow={this.props.unfollow}
        followingInProgress={this.props.followingInProgress}/>
        </>
    }
}


let mapStateToProps = (state) =>{
    return {
        users: getUsersSuper(state),
        pagesSize: getPagesSize(state),
        totalUsersCount: getTotalUsersCount(state),
        currentPage: getCurrentPage(state),
        isFetching: getIsFetching(state),
        followingInProgress: getFollowingInProgress(state)
    }
}




export default compose(
    connect(mapStateToProps,{follow,unfollow,setCurrentPage,toggleFollowingProgress,getUsers,requestUsers }))
    (UsersContainer)