import s from './Navbar.module.css'
import { NavLink } from 'react-router-dom'

const Navbar = () => {
    return (
        <nav className={s.nav}>
            <div className={s.item}>
                <NavLink to="/profile" activeClassName={s.activeLinks}>Profile</NavLink>
            </div>
            <div className={s.item}>
                <NavLink to="/dialogs"activeClassName={s.activeLinks}>Messages</NavLink>
            </div>
            <div className={s.item}>
                <NavLink to="/users"activeClassName={s.activeLinks}>Users</NavLink>
            </div>
            <div className={s.item}>
                <a>News</a>
            </div>
            <div className={s.item}>
                <a>Music</a>
            </div>
            <div className={s.item}>
                <a>Settings</a>
            </div>
        </nav>
    )
}

export default Navbar